body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  min-width: 320px;
}
a {
  color: var(--basic-black);
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
input {
  box-sizing: border-box;
}
textarea {
  box-sizing: border-box;
}
input[type="checkbox"] {
  box-sizing: border-box;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
:root {
  --basic-black: #000000;
  --basic-white: #ffffff;
  --basic-grey: #f2f2f2;
  --basic-grey-2: #666666;
  --basic-grey-3: #e7e7e7;
  --basic-light-grey: #eaeaea;
  --basic-green: #68b738;
  --basic-green-2: #5eaa2f;
  --special-grey: #cdcdcd;
  --special-grey-2: #ebebeb;
  --special-dark-grey: #444444;
  --special-dark-grey-2: #222222;
  --special-grey-3: #d9d9d9;
  --special-grey-4: #d3d3d3;
  --special-grey-5: #dcdcdc;
  --special-grey-8: #b8b8b8;
  --special-border: #e6e6e6;
  --status-error: #ff8282;
  --button-active: #8ec36d;
  --button-active-2: #b9b9b9;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 800;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 500;
  font-display: swap;
}
.button {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.5s ease 0s;
}
.button:active {
  opacity: 0.3;
}
.button:hover {
  background-color: orange;
}
